import { useState, useEffect } from 'react'
import Searcher from './components/Searcher'
import { Container } from '@mui/material';
import getUser from './services/users.js'
import UserCard from './containers/UserCard/index.jsx';


function App() {
  const [inputUser, setInputUser] = useState('octocat');
  const [userState, setUserState] = useState('inputUser');

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const data = await getUser(inputUser);
        if (inputUser == 'octocat') {
          localStorage.setItem('octocat', data);
        }
        setUserState(data);
      } catch (error) {
        const octocat = localStorage.getItem('octocat');
        setUserState(octocat);
      }
    };

    fetchUser();
  }, [inputUser]);

  return (
    <Container sx={{
      background:'whitesmoke', 
      width: '80vw', 
      minHeight: '500px',
      height: 'fit-content', 
      borderRadius: '16px',
      marginTop: '40px',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center' }}>
        <Searcher setInputUser={setInputUser}></Searcher>
        <UserCard userState={userState}></UserCard>
    </Container>
  )
}

export default App
