import React from 'react'
import {Grid, Stack, Typography} from '@mui/material'
import LocationOnIcon from '@mui/icons-material/LocationOn';
import TwitterIcon from '@mui/icons-material/Twitter';
import LanguageIcon from '@mui/icons-material/Language';
import BusinessIcon from '@mui/icons-material/Business';

const LocationInformation = ({userState}) => {
  const { location, twitter_username, blog, company } = userState;

  return (
    <Grid container spacing={2} sx={{marginTop:'15px'}}>
        <Grid item xs={6}>
            <Stack direction='row' spacing={1}>
                <LocationOnIcon/>
                <Typography>{location ? location : "-"}</Typography>
            </Stack>
        </Grid>
        <Grid item xs={6}>
            <Stack direction='row' spacing={1}>
                <TwitterIcon/>
                <Typography>{twitter_username ? <a href={`https://twitter.com/${twitter_username}/`} rel="noreferrer" target="_blank">{`@${twitter_username}`}</a> : "-"}</Typography>
            </Stack>
        </Grid>
        <Grid item xs={6}>
            <Stack direction='row' spacing={1}>
                <LanguageIcon/>
                <Typography>{blog ? <a href={blog} rel="noreferrer" target="_blank">{blog}</a> : "-"}</Typography>
            </Stack>
        </Grid>
        <Grid item xs={6}>
            <Stack direction='row' spacing={1}>
                <BusinessIcon/>
                <Typography>{company ? company : "-"}</Typography>
            </Stack>
        </Grid>
    </Grid>
  )
}

export default LocationInformation