import React from 'react'
import {Paper, Stack, Typography} from '@mui/material'

const PaperInformation = ({userState}) => {
  const { public_repos, followers, following } = userState;

  return (
    <Paper elevation={3}> 
        <Stack direction="row" spacing={4} sx={{margin: "25px 5%", justifyContent:"space-around"}}>
            <Stack>
                <Typography variant="h6">Public Repositories</Typography>
                <Typography variant="overline">{public_repos ? public_repos : 0}</Typography>
            </Stack>
            <Stack>
                <Typography variant="h6">Followers</Typography>
                <Typography variant="overline">{followers ? followers : 0}</Typography>
            </Stack>
            <Stack>
                <Typography variant="h6">Following</Typography>
                <Typography variant="overline">{following ? public_repos : 0}</Typography>
            </Stack>
        </Stack>
    </Paper>
  )
}

export default PaperInformation