import React from 'react'
import { Grid, Stack, CardMedia } from '@mui/material'
import PrincipalInformation from '../../components/PrincipalInformation';
import Description from '../Description';

const UserCard = ({userState}) => {
    const { avatar_url } = userState;

  return (
    <Grid container spacing={2} sx={{
      margin:'15px'
    }}>
        <Grid item xs={3}>
            <CardMedia sx={{borderRadius:'50%', marginLeft:'10px'}} component="img" alt="GitHub User Avatar" image={avatar_url ? avatar_url : "https://avatars.githubusercontent.com/u/583231?v=4"}/>
        </Grid>
        <Grid item xs={9}>
          <Stack direction='column' spacing={2} sx={{margin: '20px'}}>
            <PrincipalInformation userState={userState} />
            <Description userState={userState}></Description>
          </Stack>
        </Grid>
    </Grid>
  )
}

export default UserCard